.clearfix::before,.clearfix::after{content:"";display:block}
.clearfix::after{clear:both}
body{overflow-y:auto}
body.fixed{overflow-y:hidden}
.covid{width:100%;height:40px;background-color:#432f31;position:absolute;left:0;top:0;z-index:999;transition:height .3s;overflow:hidden}
.covid.cancle{height:0}
.covid .text{font-size:14px;line-height:40px;text-align:center;color:#fff}
.covid .text a{font-size:14px;padding:0 10px;color:#c7bbb4;text-decoration:underline}
.covid .close{width:30px;height:30px;position:absolute;right:5px;top:2px;z-index:999;text-indent:-9999px;overflow:hidden}
.covid .close::before{content:"";display:block;width:1px;height:25px;background-color:#fff;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute;right:15px;top:5px}
.covid .close::after{content:"";display:block;width:1px;height:25px;background-color:#fff;-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute;right:15px;top:5px}
#header {position:relative}
#header .top{padding:15px;box-sizing:border-box;display:flex;justify-content:space-between;align-items:center;position:absolute;top:40px;z-index:1000;width:100%;height:60px;background-color:#fff}
#header.cancle .top{position:fixed;top:0;}
#header .top .tab{width:20px;height:20px;position:relative}
#header .top .tab span{position:absolute;left:0;top:50%;width:100%;margin-top:-1px;border-bottom:2px solid #000}
#header .top.on .tab span{border-bottom:2px solid #fff}
#header .top .tab span::before,#header .top .tab span::after{display:block;content:"";width:100%;position:absolute;left:0;border-bottom:2px solid #000}
#header .top.on .tab span::before,#header .top.on .tab span::after{border-bottom:2px solid #fff}
#header .top .tab span::before{top:-7px}
#header .top .tab span::after{top:7px}
#header .swiper{width:100%;height:100vh;overflow:hidden}
#header .swiper-slide{text-align:center;font-size:18px;background:#fff;display:flex;justify-content:center;align-items:center}
#header .swiper-slide:first-child{background:url(/static/media/main_slider1.b43f8741.png) no-repeat center center;width:100%;background-size:cover}
#header .swiper-slide:nth-child(2){background:url(/static/media/main_slider2.be974d8f.png) no-repeat center center;width:100%;background-size:cover}
#header .swiper-slide:nth-child(3){background:url(/static/media/main_slider3.17b75f20.png) no-repeat center center;width:100%;background-size:cover}
#header .swiper-slide:last-child{background:url(/static/media/main_slider5.512047b4.png) no-repeat center center;width:100%;background-size:cover}
#header .swiper-slide .text{width:100%;position:absolute;left:0;top:50%;text-align:left;-webkit-transform:translateY(-50%);transform:translateY(-50%)}
#header .swiper-slide .text .text_inner{padding:24px}
#header .swiper-slide .text p{font-size:30px;color:#fff;font-weight:500}
#header .swiper-slide .text span{display:block;width:80%;padding-top:20px;font-size:13px;color:#fff}
#header .swiper-slide .text a{display:inline-block;font-size:13px;color:#fff;margin-top:10px;padding-right:16px;position:relative}
#header .swiper-slide .text a::after{display:block;content:"";width:6px;height:6px;border-top:1px solid #fff;border-right:1px solid #fff;-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute;right:0;top:7px}
#header .swiper-pagination .swiper-pagination-bullet{width:60px;height:2px;background:rgba(255,255,255,.5);opacity:.5;border-radius:0}
#header .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active{opacity:1;background-color:#fff}
.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet{margin:0}
#header .swiper-pagination {
    position: absolute;
    text-align: center;
    transition: .3s opacity;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    z-index: 10;
}
.menu_zone{position:fixed;right:-100%;top:0;width:100%;height:100vh;z-index:1000;background-color:#fff;transition:right .4s}
.menu_zone.active{right:0}
.menu_zone .login{display:flex;justify-content:space-between;padding:50px 24px;background-color:#444}
.menu_zone .login a{padding-right:25px;font-size:20px;color:#fff;position:relative}
.menu_zone .login a::after{display:block;content:"";width:8px;height:8px;border-top:1px solid #fff;border-right:1px solid #fff;-webkit-transform:rotate(45deg) translate(-50%);transform:rotate(45deg) translate(-50%);position:absolute;right:0;top:50%}
.menu_zone .login span{font-size:14px;color:#ad9e87}
.menu_zone .close{background-color:#444;padding:24px 24px 0;text-align:right}
.menu_zone .close span{display:inline-block;width:20px;height:2px;background-color:#fff;-webkit-transform:rotate(45deg);transform:rotate(45deg)}
.menu_zone .close span::after{display:block;content:"";width:20px;height:2px;background-color:#fff;-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}
#gnb > ul > li{border-bottom:1px solid #dcdcdc}
#gnb > ul > li > a{display:block;padding:18px 54px 18px 24px;font-size:15px;color:#333;box-sizing:border-box}
#gnb .depth2{display:none}
