#page3 .swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet,.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet{margin:0}
#page3 .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active{opacity:1;background-color:#000}
#page3 .swiper-pagination .swiper-pagination-bullet{width:calc(100%/6);height:2px;background:rgba(0,0,0,.5);opacity:.5;border-radius:0}
#page4 .news{padding:30px 24px;box-sizing:border-box}
#page4 .news .news_title{margin-bottom:10px;line-height:2;font-size:17px;font-weight:600;color:#333}
#page4 .news{background-color:#f1f1f1}
input[type=text]{padding:0 20px;font-family:"Open Sans","Noto Sans KR",sans-serif;font-size:16px;width:100%;line-height:44px;background-color:#fff;color:#333;border:1px solid #000;box-sizing:border-box}
input[type=text]::-webkit-input-placeholder{font-family:"Open Sans","Noto Sans KR",sans-serif;color:#9FA19F}
input[type=text]:-ms-input-placeholder{font-family:"Open Sans","Noto Sans KR",sans-serif;color:#9FA19F}
input[type=text]::placeholder{font-family:"Open Sans","Noto Sans KR",sans-serif;color:#9FA19F}
input[type=submit]{margin-top:20px;padding:0 20px;width:100%;line-height:44px;font-family:"Open Sans","Noto Sans KR",sans-serif;font-size:16px;background-color:#111;color:#fff;border:none}
#page4 .news .apply .desc{margin-top:20px;font-size:14px;color:#666}
#page4 .news .apply .desc span{color:#333;font-weight:600;text-decoration:underline}
#page4 .bottom{font-size:13px;line-height:20px;padding:50px 20px;color:#999;text-align:center}
footer a{color:#a7a7a7}
footer .ft_top{padding:25px 15px;background-color:#555}
footer .ft_top .sns{box-sizing:border-box;text-align:center}
footer .ft_top .sns li{display:inline-block;padding:10px 15px}
footer .ft_top li:first-child,footer .ft_top li:nth-child(2){padding:10px 25px}
footer .ft_top li img{width:25px;height:21px}
footer .ft_top li:first-child img,footer .ft_top li:nth-child(2) img{width:100%}
footer .ft_center{padding:25px 15px;background-color:#111;border-bottom:1px solid #a7a7a7}
footer .ft_center .inner .copyright{font-size:15px;color:#999}
footer .ft_center .inner .copyright p{text-align:center}
footer .ft_bottom{padding:25px 0;text-align:center;background-color:#111}
footer .ft_bottom .inner li{display:inline-block;position:relative}
footer .ft_bottom .inner li a{display:block;padding:0 15px;font-size:14px}
footer .ft_bottom .inner li:first-child a{padding:0 15px 0 0}
footer .ft_bottom .inner li:first-child::after{display:block;content:"";width:1px;height:12px;background-color:#a7a7a7;position:absolute;right:0;top:5px}
@media only screen and (orientation:landscape) {
.menu_zone{overflow-y:auto}
#page1 .inner li{width:calc((100% - 20px) / 3);margin-left:10px}
#page1 .inner li:first-child{margin:0}
#page1 .inner li .photo{height:150px}
#page1 .inner .desc h3{font-size:18px}
#page1 .inner .desc p{font-size:13px}
#page2 .content p{white-space:normal;text-overflow:clip;display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2}
footer .ft_top li:first-child,footer .ft_top li:nth-child(2){padding:10px 15px}
}
